.featured .row{
    display: flex;
    flex-wrap: wrap;
    gap:.5rem;
    border:.1rem solid rgba(0,0,0,.1);
    border-radius: .5rem;
    background:#f9f9f9;
    padding:1rem;
    align-items: center;
  margin: 20px 40px;
    min-width: 200px;
    justify-content: center;
  }
  
  .featured .row .content{
    flex:1 1 20rem;
  }
  
  .featured .row{
    display:flex;
    gap:1rem;
    align-items: center;
    flex:1 1 40rem;
  }
  
  
  .featured .row .big-image{
    max-width: 700px;
    align-items: center;
    display: flex;
    padding:1rem;
  }
  
  
  .featured .row .big-image img{
    max-width:100%;
    width:clamp(14rem, 10rem, 9rem);
    display: flex;
    overflow: hidden;
    
  }
  
  .featured .row .big-image img:hover{
    transform: scale(1.05);
  }
   
  .featured .row .content h3{
    font-size: 1.5rem;
    color:var(--MAIN);
  }
  .color-content{
    margin-top: 3.2rem;
  }
  .color-content h3{
    text-transform: uppercase;
    font-size: 1.6rem;
  }
  .color{
    width: 35px;
    height: 35px;
    background: none;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-right: 12px;
    cursor: pointer;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
  }
  .color2{
    width: 35px;
