  }
  .color-black{
    background-color: #1c1c1b;
  }
  .color:hover{
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.3);
  }
  .active-color{
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.3);
  }
  .color-groups{
    padding: 1rem 0;
    display: flex;
  }
  
  
  .featured .row .content .stars i{
    font-size: 1rem;
    color:var(--HOVER);
    padding:.5rem 0;
  }
  
  .featured .row .content p{
    font-size: 1rem;
    color:#666;
    padding:1rem 0;
  }
  
  .featured .row .content .price{
    font-size: 2.5rem;
    color:var(--MAIN);
    font-weight: bolder;
  }
  
  .featured .row .content .past-price{
    font-size: 1.5rem;
    color:red;
    text-decoration: line-through;
  }
  